<template>
  <Container class="RuleSelect" title="RULE SELECT">
    <div class="RuleSelect-wrapper">
      <template v-for="item in items" :key="item.value">
        <Radio
          name="rule-select"
          :label="item.value"
          :value="item.value"
          :checked="item.checked"
          @click="item.event"
        />
      </template>
    </div>
  </Container>
</template>
<script>
import { ref } from "vue";
import { useStore } from "vuex";
import Container from "@/components/atoms/Container.vue";
import Radio from "@/components/atoms/Radio.vue";
import { ShowModal, SetMode } from "@/store/actionTypes";
export default {
  name: "RuleSelect",
  components: {
    Container,
    Radio,
  },
  setup() {
    const store = useStore();
    const items = ref([
      {
        value: "RANDOM",
        checked: true,
        event: () => store.dispatch(SetMode, "random"),
      },
      {
        value: "INPUT",
        checked: false,
        event: () => store.dispatch(ShowModal),
      },
    ]);
    store.dispatch(SetMode, items.value.find((item) => item.checked).value);
    return {
      items,
    };
  },
};
</script>
<style scoped>
.RuleSelect-wrapper {
  display: flex;
  flex-direction: row;
  gap: 15px;
}
</style>